import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router';
import {AuthService} from '../services/auth.service';

@Injectable({ providedIn: 'root' })

// La garde d'authentification est utilise pour empcher les utilisateurs non authentifis
// d'accder aux pages rserves aux utilisateurs connects.
export class AuthGuard implements CanActivate {
    constructor(
        private router: Router,
        private authService: AuthService
    ) {
    }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        const currentUser = this.authService.currentUserValue;
        if (currentUser) {
            // Autoris donc retourne vrai
            return true;
        }

        // Non connect donc redirection vers la page de connexion avec l'url retourn
        this.router.navigate(['/login'], {queryParams: { returnUrl: state.url}});
        return false;
    }
}
